.App {
  text-align: center;
}

.row {
  display: flex;
  flex-flow: row wrap;
  width: 100%;
  flex: 1;
  min-width: 7.5rem;

  .col-1 {
    flex-basis: 8.33%
  }

  .col-2 {
    flex-basis: 16.66%
  }

  .col-3 {
    flex-basis: 25%
  }

  .col-4 {
    flex-basis: 33.33333333%;
  }

  .col-5 {
    flex-basis: 41.66%
  }

  .col-6 {
    flex-basis: 50%
  }

  .col-7 {
    flex-basis: 58.33%
  }

  .col-8 {
    flex-basis: 66.66%
  }

  .col-9 {
    flex-basis: 75%
  }

  .col-10 {
    flex-basis: 83.33%
  }

  .col-11 {
    flex-basis: 91.66%
  }

  .col-12 {
    flex-basis: 100%
  }

}

img {
  max-width: 100%;
}

