.nav-bar {
    display: flex;
    gap: 50px;
    padding: 0px;
    box-shadow: 1px 0px 1px 1px;
    align-items: center;
    position: fixed;
    background-color: white;
    top: 0;
    width: 100%;
    z-index: 113545;
}

.nav-bar Link {
    text-decoration: none;
}

.nav-items {
    display: flex;
    gap: 20px;
    font-weight: 600;
    font-size: 14px;
    align-items: center;
    cursor: pointer;
}

.nav-items li {
    list-style: none;
}

.new {
    color: #FF3F6C;
}

.nav-items2 {
    display: flex;
    gap: 20px;
    font-weight: 600;
    font-size: 14px;
    cursor: pointer;
    padding-left: 10px;
}

.nav-items2 li {
    list-style: none;
}

.nav-logo {
    padding-left: 20px;
}

.nav-logo img {
    height: 80px;
    width: 100px;
    cursor: pointer;
}

.nav-search input {
    width: 450px;
    height: 35px;
    border: none;
    background-color: rgba(238, 237, 237, 0.889);
    font-size: 14px;
}

.font-icon {
    text-align: center;
    position: relative;
}

.cart-length {
    position: absolute;
    bottom: 25px;
    left: 20px;
    border: none;
    border-radius: 20px;
    padding: 3px 7px;
    background-color: blueviolet;
    color: white;
}

.bag-nav-bar {
    display: flex;
    justify-content: space-between;
    box-shadow: 1px 0px 1px 1px;
    align-items: center;
    position: fixed;
    background-color: white;
    top: 0;
    width: 100%;
    z-index: 113545;
    font-size: 12px;
    color: #696B79;
}

.bag-process {
    display: flex;
    gap: 5px;

}

.active {
    display: none;
}

.secure-icon {
    display: flex;
    align-items: center;
    margin: 20px;
}

.secure-icon img {
    height: 30px;
    width: 30px;
}

.secure-icon div {
    letter-spacing: 4px;
    margin-left: 3px;
}


@media screen and (max-width:1350px) { 
    .nav-bar {

        .nav-items,
        .nav-items2 {
            font-size: 10px;
        }

        .nav-items {
            display: flex;
            flex-direction: column;
            position: absolute;
            top: 70px;
            left: 70px;
            background-color: white;
            width: 200px;
            align-items: stretch;
        }

        .active-item {
            display: none;
        }

        .nav-search {
            display: none;
        }

        .active {
            display: block; 
        }
        .nav-logo img {
            height: 60px;
            width: 80px;
            cursor: pointer;
        }
    }
}