.cart {

    text-align: center;
    margin: 100px;

    .col-5,
    .col-7 {
        padding: 0px;
    }

}

.imag-details {

    .imag-title,
    .imag-discription,
    .imag-qty,
    .imag-price {
        font-size: 14px;
    }

    .imag-title {
        cursor: default;
    }

    .imag-seller,
    .return {
        font-size: 12px;
    }

    .return {
        cursor: default;
    }

    .imag-qty,
    .imag-discription {
        color: #282C3F;
        cursor: pointer;
    }

    .imag-seller {
        color: #94969F;
    }

    .imag-price {
        cursor: default;

        b {
            color: #282C3F;
        }

        del {
            color: #94969F;
        }

        span {
            color: #F16565;
        }
    }
}

.add-more-to-wishlist {
    display: flex;
    justify-content: space-between;
    margin: 10px;
    border: solid 1px;
    padding: 20px;
    font-size: 14px;
    color: #282C3F;
    border-radius: 4px;
    border-color: #94969f27;
}

.cart-right {
    border-left: solid 1px;
    border-color: #94969f27;
}

.apply-coupons {
    text-align: start;
    margin: 10px;
    border-bottom: solid 1px;
    border-color: #94969f3e;

    .coupons {
        display: flex;
        justify-content: space-between;
        padding: 10px;
    }

    .coupons button {
        padding: 4px 16px;
        color: crimson;
        border: solid 1px;
        font-weight: 800;
        background-color: white;
        font-size: 12px;
    }

    .coupan-icon {
        display: flex;
        gap: 10px;
    }
}

.apply-coupons h3 {
    font-size: 13px;
}

.gift {
    text-align: start;
    margin: 10px;
    border-bottom: solid 1px;
    border-color: #94969f43;

    .gift-card {
        display: flex;
        gap: 15px;
        background-color: #FFF1EC;
        margin: 10px;
        text-align: start;
        line-height: 10px;
    }

    .gift-card img {
        height: 120px;
    }

    .gift-card button {
        border: none;
        background-color: #FFF1EC;
        color: #FF3F6C;
        font-weight: 800;
    }

    .gift-card .title {
        font-size: 14px;
    }

    .gift-card .discription {
        font-size: 12px;
    }
}

.gift h3 {
    font-size: 12px;
}

.price-details {
    text-align: start;
    margin: 10px;
    border-bottom: solid 1px;
    border-color: #94969f27;

    .total-price {
        display: flex;
        justify-content: space-between;
        margin: 10px;
    }

    .price-title {
        font-size: 14px;
        color: #282C3F;
    }

    .price-title button {
        border: none;
        background-color: white;
        color: #FF3F6C;
        font-weight: 800;
    }

    .price {
        font-size: 14px;
    }

    .discount {
        color: #03A685;
    }

    .discount-coupan {
        color: #FF3F6C;
    }

    .shippig-fee span {
        color: #03A685;
    }
}

.price-details h3 {
    font-size: 12px;
}

.total-amount {
    margin: 20px;

    .totalAmount {
        display: flex;
        justify-content: space-between;
        font-size: 15px;
        color: #3E4152;
    }

    button {
        width: 100%;
        padding: 10px 0px;
        border: none;
        background-color: #FF3F6C;
        color: white;
        font-weight: 800;
    }
}

.cart-bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-top: solid 1px;
    border-color: #94969f27;

    img {
        height: 37px;
        width: 70px;
        padding: 5px;
    }

    .contact-us {
        font-size: 14px;
        color: #282C3F;
        font-weight: 700;
    }
}

.checkbox-wrapper {
    gap: 0px;
}

.checkbox-wrapper .quqntity {
    border: 1px solid;
    border-radius: 35px;
    padding: 5px;
    outline: none;
    cursor: default;
    line-height: 25px;
    margin-left: 18px;
    margin-bottom: 15px;
    margin-top: 15px;
}

.checkbox-wrap {
    margin-top: -20px;
    margin-left: 10px;
}

.checkbox-wrap .size {
    border: 1px solid;
    outline: none;
    cursor: default;
    padding: 15px 16px;
    border-radius: 28px;
    line-height: 13px;
    margin: 15px 16px 12px 0px;
    font-size: 16px;
}

.checked {

    color: #FF3F6C;
    border-color: #F16165;

    input[type="checkbox"] {
        border-color: #F16165;
    }
}

.unchecked {
    background-color: red;
}

.enter-delivery-details {
    position: fixed;
    z-index: 12344;
    height: 1500px;
    width: 1500px;
    top: 0px;
    left: 0px;
    background-color: #3e415269;

    .delivery-details {
        background-color: #F5F5F6;
        width: 450px;
        z-index: 2324;
        position: relative;
        left: 500px;
        top: 150px;
        border-radius: 10px;

        .delivery-details-heading {
            display: flex;
            justify-content: space-between;
            padding: 20px;
            font-weight: 800;
            font-size: 16px;
            background-color: white;

            img {
                height: 18px;
                width: 18px;
                cursor: pointer;
            }

            .title {
                cursor: default;
            }
        }

        .enter-pincode {
            margin-top: 10px;
            background-color: white;
            padding-top: 15px;

            #pincode {
                width: 400px;
                height: 40px;
                border-radius: 3px;
                font-size: 14px;
                border-width: 1px;
                border-color: #94969f5f;
            }

            #check {
                position: relative;
                border: none;
                bottom: 30px;
                left: 160px;
                background-color: white;
                color: #5d5d5efb;
                cursor: pointer;
            }
        }

        .or {
            font-size: 14px;
            padding: 10px 0px;
            cursor: default;
        }

        .add-new-address {
            padding-top: 10px;
            background-color: white;
            padding-bottom: 10px;

            button {
                width: 440px;
                padding: 9px 0px;
                background-color: white;
                border-width: 1px;
                border-radius: 3px;
                font-size: 12px;
            }
        }
    }
}
.cart-items{
    margin-top: 100px;
}